@import "base.scss";

section.features {
	grid-column: center-start / center-end;
	margin: 15rem 0;

	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
	gap: 6rem;

	.feature {
		display: grid;
		grid-template-columns: min-content auto;
		gap: 1.5rem 2.5rem;

		&__icon {
			fill: $color-primary;
			width: 4.5rem;
			height: 4.5rem;
			grid-row: 1 / span 2;
		}

		&__text {
			font-size: 1.7rem;
		}
	}
}
